import React, { Fragment } from 'react'
import styled from 'styled-components'
import TagList from './TagList'
import Translations from './Translations'
import Time from './Time'
import { colors } from '../tokens'
import { Bull } from './Commons'
import Wrapper from './Wrapper'

const Header = styled.header`
  margin-bottom: 2rem;
  color: ${colors.textLight};
  font-size: 0.9em;
`

const InlineHeader = styled.header`
  margin-bottom: 2rem;
  color: ${colors.textLight};
  font-size: 2em;
`

class ContentIntro extends React.Component {
  render() {
    const { title, date, tags, translations } = this.props        
    return (      
      <Header>
        {<InlineHeader>{title}</InlineHeader>}
        <br />
        {date && <Time date={date} />}
        <br />
        {date && Array.isArray(tags) && tags.length > 0 && 'TAGS'} {/* && <Bull /> */}        
        {Array.isArray(tags) && tags.length > 0 && (
          <Fragment>
            <TagList tags={tags} />            
          </Fragment>
        )}

        {translations && <Translations translations={translations} />}
      </Header>
    )
  }
}

export default ContentIntro
